.about-section.about-2--1 {
	.block-title {
		background-color: transparent;
		text-align: justify;
		border-bottom: 0;
		justify-content: center;

		.about-title {
			color: map-get($mau, main);
			display: inline-block;
			border-bottom: 1px solid #707070;
		}

		p {
			color: #888888;
			font-size: 16px;

			@media (max-width: 1199.98px) {
				font-size: 15px;
			}

			@media (max-width: 767.98px) {
				font-size: 14px;
			}
		}
	}

	@media (min-width: 1025px) {
		.block-title {
			justify-content: flex-start;
			padding: 0 45px 0 0;
			text-align: left;
		}

		.col-title {
			display: flex;
			align-items: center;
		}

		.row-custom {
			margin: -35px -18px 0 -18px 0;

			.col,
			[class*="col-"] {
				padding-right: 18px;
				padding-left: 18px;
			}
		}
	}
}

.about-2--1--item {
	margin-top: 20px;

	a {
		display: block;
		position: relative;
		overflow: hidden;
	}

	.imgbox {
		height: 135px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (min-width: 768px) {
			height: 150px;
		}

		@media (min-width: 1025px) {
			height: auto;
		}
	}

	.content {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		top: 0;
		background-color: rgba(black, 0.6);
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px 10px;
		transition: all 0.2s ease-in-out;

		h3 {
			color: white;
			font-size: 16px;
			font-weight: 400;
			text-transform: uppercase;

			@media (max-width: 1199.98px) {
				font-size: 15px;
			}

			@media (max-width: 767.98px) {
				font-size: 14px;
			}
		}
	}

	@media (max-width: 1024.98px) {
		.content {
			position: static;
			padding: 10px 0;
			background: #f6f6f6;

			h3 {
				color: #1C1C1C;
			}
		}
	}

	@media (min-width: 1025px) {
		margin-top: 35px;

		.content {
			top: auto;
			height: 60px;
			background-color: rgba(black, 0.8);

			h3 {
				color: white;
				font-size: 16px;
				font-weight: 400;
				text-transform: uppercase;

				@media (max-width: 1199.98px) {
					font-size: 15px;
				}

				@media (max-width: 767.98px) {
					font-size: 14px;
				}
			}
		}

		&:hover {
			.content {
				height: 100%;
			}
		}
	}
}