.about-5 {
	.about-title {
		color: map-get($mau, main);
		margin-bottom: 10px;
	}

	.content-wrapper {
		color: #888888;
		margin-top: 28px;

		ul {
			list-style-type: none;
		}

		.row {
			+.row {
				margin-top: 10px;
			}

			@media (min-width: 768px) {
				&:nth-child(even) {
					flex-direction: row-reverse;
				}
			}

			@media (min-width: 1025px) {
				+.row {
					margin-top: 20px;
				}
			}
		}

		.imgbox,
		.content {
			img {
				width: 100%;
			}

			@media (min-width: 1025px) {
				height: 100%;
				padding: 0 20px;
				display: flex;
				justify-content: center;
				flex-direction: column;
			}
		}

		.imgbox {
			@media (max-width: 767.98px) {
				margin-top: 20px;
			}
		}

		@media (min-width: 1025px) {
			padding: 0 40px;
		}
	}
}

.about-5--2 {
	background-color: #f6f6f6;

	.about-title {
		color: map-get($mau, main);
		margin-bottom: 10px;

		@media (max-width: 1024.98px) {
			padding-top: 20px;
			padding-bottom: 20px;
			font-size: 20px;
		}

		@media (max-width: 767.98px) {
			font-size: 17px;
		}
	}
}

.about-5--2-slider {
	position: relative;

	.swiper-button-next {
		background-image: url(../img/icons/next-brand.svg);
		background-size: 8px;
		background-repeat: no-repeat;
		height: 30px;
		width: 30px;
		transform: translateY(-50%);
		margin-top: unset;
		right: -40px;
	}

	.swiper-slide {
		.about-2--1--item .content {
			top: auto;
		}
	}

	.swiper-button-prev {
		background-image: url(../img/icons/prev-brand.svg);
		background-size: 8px;
		background-repeat: no-repeat;
		height: 30px;
		width: 30px;
		transform: translateY(-50%);
		margin-top: unset;
		left: -40px;
	}

	@media (max-width: 1024px) {
		padding: 0 30px;

		.swiper-button-next {
			right: 0;
		}

		.swiper-button-prev {
			left: 0;
		}
	}
}