.about-4 {
	background: white;

	p {
		font-size: 16px;
		margin-bottom: 40px;

		@media (max-width: 1199.98px) {
			font-size: 15px;
			margin-bottom: 25px;
		}

		@media (max-width: 767.98px) {
			font-size: 14px;
			margin-bottom: 10px;
		}
	}

	.row-custom {
		margin: 10px -5px 0 -5px;

		.col,
		[class*="col-"] {
			padding-right: 5px;
			padding-left: 5px;
		}

		@media (min-width: 1025px) {
			margin: 30px -15px 0 -15px;

			.col,
			[class*="col-"] {
				padding-right: 15px;
				padding-left: 15px;
			}
		}
	}

	.pagination {
		justify-content: center;
	}
}

.about-4--item--title {
	position: relative;

	a {
		display: block;
	}

	.imgbox {
		img {
			width: 100%;
		}
	}

	.content {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(black, 0.6);
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		h3 {
			font-size: 18px;
			text-transform: uppercase;
			color: white;

			@media (max-width: 1199.98px) {
				font-size: 17px;
			}

			@media (max-width: 767.98px) {
				font-size: 16px;
			}
		}
	}
}

.about-4--item--col {
	margin-top: 10px;

	@media (min-width: 1025px) {
		margin-top: 30px;
	}
}

.about-4--item {
	position: relative;

	.imgbox {
		a {
			display: block;
		}

		img {
			width: 100%;
		}
	}

	.content {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(black, 0.6);
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		padding: 10px 5px;

		h3 {
			font-size: 18px;
			text-transform: uppercase;
			text-align: center;
			color: white;

			@media (max-width: 1199.98px) {
				font-size: 17px;
			}

			@media (max-width: 767.98px) {
				font-size: 16px;
			}

			@media (max-width: 575.98px) {
				font-size: 13px;
				font-weight: 400;
			}
		}

		@media (max-width: 1024.98px) {
			text-align: center;
			height: auto;
			bottom: 0;
		}
	}

	@media (min-width: 1025px) {
		.content {
			opacity: 0;
			transition: all 0.2s ease-in-out;
		}

		&:hover {
			.content {
				opacity: 1;
			}
		}
	}
}

.about-duan-wrapper {
	p {
		font-size: 14px;

		@media (min-width: 768px) {
			font-size: 15px;
		}

		@media (min-width: 1200px) {
			font-size: 16px;
		}
	}

	>.row {
		justify-content: center;
	}
}

.project-list {
	.row-custom {
		justify-content: center;
	}

	.pagination {
		margin-top: 25px;
		justify-content: center;
	}
}

.about-project {
	justify-content: center;

	.row-custom {
		margin: 10px -5px 0 -5px;

		.col,
		[class*="col-"] {
			padding-right: 5px;
			padding-left: 5px;
		}

		@media (min-width: 1025px) {
			margin: 10px -15px;

			.col,
			[class*="col-"] {
				padding-right: 15px;
				padding-left: 15px;
			}
		}
	}
}

.about-ajax {
	.pagination {
		margin-top: 25px;
		justify-content: center;
	}
}

.project-page {
	background-color: white;
}

.about-page {
	main {
		>.breadcrumb-wrapper {
			display: none !important;
		}
	}
}