::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
	background-color: #fff;
}

::-webkit-scrollbar {
	width: 5px;
	height: 5px;
	background-color: #fff;
}

::-webkit-scrollbar-thumb {
	border-radius: 2.5px;
	box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
	background-color: #D82F25;
}

#phone-call {
	cursor: pointer;
	position: fixed;
	left: 25px;
	bottom: 18pt;
	z-index: 200;

	.phone-fixed {
		display: block;
		position: relative;
		transform: translateZ(0) scale(1);
		margin: 0;
		box-shadow: 0 0 0 0 rgba(216, 47, 37, 0), 0 0.05em 0.1em rgba(0, 0, 0, .2);
		border-radius: .5em;
		background-color: #ffffff;
		width: 60px;
		height: 60px;
		font-size: 7vmin;

		@media (max-width: 767.98px) {
			font-size: 24vmin;
		}
	}

	.phone-fixed::before,
	.phone-fixed::after {
		position: absolute;
		content: "";
	}

	.phone-fixed::before {
		top: 0;
		left: 0;
		transform: translateZ(0) scale(0);
		transform-origin: 50% 50%;
		opacity: 1;
		border-radius: 100%;
		background-color: hsla(0, 0%, 100%, .1);
		width: 100%;
		height: 100%;
	}

	.phone-fixed::after {
		top: 0;
		left: 0;
		transform: translateZ(0);
		background: url(../img/phone-fixed.svg);
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-size: 22px;
		width: 100%;
		height: 100%;
	}

	.phone-fixed.is-animating {
		animation: phone-outer 3000ms infinite;

		&::before {
			animation: phone-inner 3000ms infinite;
		}

		&::after {
			animation: phone-icon 3000ms infinite;
		}
	}

	@keyframes phone-outer {
		0% {
			transform: translate3d(0, 0, 0) scale(1);
			box-shadow:
				0 0 0 0em rgba(#3498db, 0),
				0em 0.05em 0.1em rgba(#000000, 0.2);
		}

		33.3333% {
			transform: translate3d(0, 0, 0) scale(1.1);
			box-shadow:
				0 0 0 0em rgba(#3498db, 0.1),
				0em 0.05em 0.1em rgba(#000000, 0.5);
		}

		66.6666% {
			transform: translate3d(0, 0, 0) scale(1);
			box-shadow:
				0 0 0 0.5em rgba(#3498db, 0),
				0em 0.05em 0.1em rgba(#000000, 0.2);
		}

		100% {
			transform: translate3d(0, 0, 0) scale(1);
			box-shadow:
				0 0 0 0em rgba(#3498db, 0),
				0em 0.05em 0.1em rgba(#000000, 0.2);
		}
	}

	@keyframes phone-inner {
		0% {
			opacity: 1;
			transform: translate3d(0, 0, 0) scale(0);
		}

		33.3333% {
			opacity: 1;
			transform: translate3d(0, 0, 0) scale(0.9);
		}

		66.6666% {
			opacity: 0;
			transform: translate3d(0, 0, 0) scale(0);
		}

		100% {
			opacity: 0;
			transform: translate3d(0, 0, 0) scale(0);
		}
	}

	@keyframes phone-icon {
		0% {
			transform: translate3d(0em, 0, 0);
		}

		2% {
			transform: translate3d(0.01em, 0, 0);
		}

		4% {
			transform: translate3d(-0.01em, 0, 0);
		}

		6% {
			transform: translate3d(0.01em, 0, 0);
		}

		8% {
			transform: translate3d(-0.01em, 0, 0);
		}

		10% {
			transform: translate3d(0.01em, 0, 0);
		}

		12% {
			transform: translate3d(-0.01em, 0, 0);
		}

		14% {
			transform: translate3d(0.01em, 0, 0);
		}

		16% {
			transform: translate3d(-0.01em, 0, 0);
		}

		18% {
			transform: translate3d(0.01em, 0, 0);
		}

		20% {
			transform: translate3d(-0.01em, 0, 0);
		}

		22% {
			transform: translate3d(0.01em, 0, 0);
		}

		24% {
			transform: translate3d(-0.01em, 0, 0);
		}

		26% {
			transform: translate3d(0.01em, 0, 0);
		}

		28% {
			transform: translate3d(-0.01em, 0, 0);
		}

		30% {
			transform: translate3d(0.01em, 0, 0);
		}

		32% {
			transform: translate3d(-0.01em, 0, 0);
		}

		34% {
			transform: translate3d(0.01em, 0, 0);
		}

		36% {
			transform: translate3d(-0.01em, 0, 0);
		}

		38% {
			transform: translate3d(0.01em, 0, 0);
		}

		40% {
			transform: translate3d(-0.01em, 0, 0);
		}

		42% {
			transform: translate3d(0.01em, 0, 0);
		}

		44% {
			transform: translate3d(-0.01em, 0, 0);
		}

		46% {
			transform: translate3d(0em, 0, 0);
		}
	}

	@media (max-width: 1024.98px) {
		left: unset;
		right: 25px;
		bottom: 80pt;
	}
}

#messenger {
	cursor: pointer;
	position: fixed;
	right: 25px;
	bottom: 18pt;
	z-index: 200;
}

.block-title-mb {
	display: none;
	position: relative;
	text-align: center;
	background: #ffffff;
	padding: 20px 0 25px;
	border-radius: 5px 5px 0 0;
	border-bottom: 1px #70707050 solid;

	.main-title-mb {
		margin: 0 auto;
		display: inline-block;
		color: #000000;
		font-weight: 700;
		font-size: 21px;
		cursor: pointer;

		&.active {
			border-bottom: #D82F25 .5px solid;
		}
	}

	@media(max-width: 1024px) {
		display: block;
		padding: 15px 0;
		text-align: left;

		.main-title-mb {
			display: block;
			font-size: 14px;
			margin: 0 0 0 25px;
		}
	}
}

.block-title {
	position: relative;
	text-align: center;
	background: #D82F25;
	padding: 8px 15px;
	border-radius: 5px 5px 0 0;
	border-bottom: 1px #70707050 solid;

	.main-title {
		margin: 0 auto;
		display: inline-flex;
		align-items: center;
		color: #000000;
		font-weight: 500;
		font-size: 16px;
		line-height: 1.5;
		cursor: pointer;
	}

	.main-title {
		color: white;
	}

	.block-nav {
		height: auto;
		width: 600px;
		border-left: 1px solid white;
		padding: 0 25px;
		position: relative;

		a {
			color: white;
			border: 1px solid white;
			font-size: 13px;
			border-radius: 14px;
			border: 1px solid white;
			padding: 2px 8px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;

			&:hover,
			&.active {
				background: white;
				border: 1px solid white;
				color: #D82F25;
			}
		}

		.swiper-container {
			height: 100%;
		}

		.swiper-prev,
		.swiper-next {
			z-index: 5;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
			display: flex;
			justify-content: center;
			align-items: center;
			cursor: pointer;

			&.swiper-button-disabled {
				opacity: 0;
			}

			span {
				display: flex;
				align-items: center;
				color: white;
			}
		}

		.swiper-prev {
			left: 5px;
		}

		.swiper-next {
			right: 5px;
		}

		.swiper-slide {
			height: 100%;
			display: flex;
			width: auto;
			align-items: center;
			justify-content: center;
		}

		@media (max-width: 1024px) {
			width: 400px;
		}

		@media (max-width: 768px) {
			border-left: 0;
		}
	}

	&.sale-hot {
		@media(max-width: 1024px) {
			display: none;
		}

		&::after {
			content: '';
			display: block;
			position: absolute;
			height: 100%;
			width: 1px;
			background: #70707050;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
		}

	}

	&.block-left {
		text-align: left;
		display: flex;

		.see-more {
			display: inline-flex;
			height: auto;
			border: 0;
			background: transparent;
			color: white;
			margin-left: auto;
			text-decoration: underline;
		}

		.main-title {
			line-height: 1.5;
			margin-left: 0;
		}
	}

	@media(max-width: 1024px) {
		display: block;
		// padding: 8px 15px;
		text-align: left;

		.main-title {
			display: block;
			font-size: 17px;
			margin: 0 0 0 25px;
			margin-right: auto;
		}
	}

	@media (max-width: 767px) {
		flex-wrap: wrap;
		padding: 8px 0;

		.main-title {
			margin-left: 0;
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0 15px 15px 15px;
		}

		.block-nav {
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0 45px;
			border-left: 0;
		}
	}

}

.rate {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin-bottom: 14px;

	.img-star {
		cursor: pointer;
		flex: 0 0 115px;
		position: relative;
		width: 115px;
		height: 23px;
		margin-left: -4px;
		margin-right: 20px;
	}

	.star {
		background-image: url(../img/icons/star-nofull.svg);
		background-repeat: repeat-x;
		background-size: 23px;
		height: 100%;
	}

	.star-rated {
		position: absolute;
		top: 0;
		height: 100%;
		background-image: url(../img/icons/star-full.svg);
		background-repeat: repeat-x;
		background-size: 23px;
		z-index: 3;
		pointer-events: none;
	}

	@media(max-width: 1024px) {
		margin-bottom: 0;

		.img-star {
			cursor: pointer;
			flex: 0 0 90px;
			position: relative;
			width: 90px;
			height: 18px;
			margin-right: 20px;
		}

		.star {
			background-size: 18px;
		}

		.star-rated {
			background-size: 18px;
		}
	}
}

.badge-sale-hot {
	background: #D82F25;
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0;
	right: 0;
	height: 35px;
	width: 35px;
	border-radius: 50%;
	font-size: 12px;

	span {
		font-family: "Helvetica-Neue", sans-serif;
		color: #ffffff;
		font-weight: 700;
	}
}

.time {
	display: inline-block;
	position: relative;
	font-size: 12px;
	color: #1C1C1C;
	padding-left: 25px;

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		background-image: url(../img/icons/time-news-black.svg);
		width: 15px;
		height: 15px;
		background-size: 13px;
		background-repeat: no-repeat;
	}
}

#backdrop,
#header-backdrop {
	background: rgba(0, 0, 0, 0.8);
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 20;
	display: none;

	&.active {
		display: block;
	}
}

.clearfix::after {
	content: "";
	clear: both;
	display: table;
}

.hide-in-mobile {
	@media(max-width:1024px) {
		display: none !important;
	}
}

.hide-in-pc {
	display: none;

	@media(max-width: 1024px) {
		display: block;
	}
}

.newsDetail-page {
	@media(max-width: 1024px) {
		.list-same-news {
			display: none;
		}
	}
}

.list-product {
	@media (max-width: 1024.98px) {
		margin-top: 30px;
	}
}

.ui-widget-content {
	.ui-state-default {
		background: white;
		border: 1px solid #D82F25;
	}
}

.ui-slider-horizontal {
	.ui-slider-handle {
		top: -0.5rem;
	}
}

.lcl {
	overflow: hidden;
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;

	&-1 {
		-webkit-line-clamp: 1;
	}

	&-2 {
		-webkit-line-clamp: 2;
	}

	&-3 {
		-webkit-line-clamp: 3;
	}
}

.ov-h {
	overflow: hidden;
}

.ofcv {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
	font-family: 'object-fit: cover, object-position: center';
	background-size: cover;
}

.mx-at {
	margin: 0 auto;
}